.App {
  text-align: center;
  font-size: 1.1em;
}

button {
  background-color: #00a7db;
  color: white;
  padding: 10px;
  border: none;
  font-weight: bold;
  cursor: pointer;
  border-radius: 20px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

button:hover {
  background-color: #009aca;
}

#titleDiv {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-bottom: 20px;
}


#title {
  font-weight: bold;
  -ms-flex-item-align: center;
      align-self: center;
  font-size: 40px;
}

.name {
  color: #009aca;
  font-size: 25px;
  margin: 0px;
  margin: -5px;
  font-weight: bold;
}

strong {
  color: #009aca;
}

#loading {
    margin: 20px;
    width: 100px; 
    height: 100px;    
    -webkit-animation: spin 1.5s infinite linear;    
            animation: spin 1.5s infinite linear;
}

@-webkit-keyframes spin {
    0%  {-webkit-transform: rotate(0deg);}
    100% {-webkit-transform: rotate(360deg);}   
}

img {
  border-radius: 15px
}


